import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { Agencia, CLASS_NAME } from '../../../../models/dtos/ger/agencia';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'numero',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const AgenciaSearchView: FC<SearchViewProps<Agencia>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'numero',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Número',
    },
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome',
    },
    {
      dataField: 'banco.nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Banco',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Agência">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'AgenciaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default AgenciaSearchView;
